		overflow: hidden;
		border: 1px solid pink;
		width: 100%;
		position: relative;
		@media screen and (max-width: 720px) {
			height: 745px; }
		&_notShow {
			display: none; } }

	&-fade {
		animation-name: fade;
		animation-duration: 1.5s; }
	&-person-photo_container {
		@include col();
		@include size(4);
		@include size-md(5);
		@include size-sm(10);
		@include size-xs(12);
		@include shift-left(1);
		@media screen and (max-width: 992px) {
			@include shift-left(0); }
		@media screen and (max-width: 720px) {
			margin-bottom: 1.5rem; } }
	&-person-photo_box {
		width: 285px;
		height: 365px;
		position: relative;
		@media screen and (max-width: 720px) {
			display: block;
			margin: 0 auto; } }
	&-person-photo_overlay {
		box-sizing: border-box;
		position: absolute;
		top: 0.5rem;
		left: 0.5rem;
		width: 275px;
		height: 353px;
		background-color: rgba(255,255,255, 0.5);
		opacity: 0;
		&:hover {
			opacity: 1; } }
	&-person-photo_content {
		text-align: center;
		margin-top: 50%;
		li {
			display: inline-block;
			text-align: center; }
		.icon {
			font-size: 1.5rem;
			margin-left: 0;
			margin-right: 1rem;
			padding: 0.5rem; } }

	&-person_description {
		@include col();
		@include size(6);
		@include size-md(7);
		@include size-sm(12);
		@include shift-right(1);
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		color: $main-text;
		height: 365px;
		overflow-y: auto;
		padding-right: 2rem;
		@media screen and (max-width: 992px) {
			@include shift-right(0); }
		&::-webkit-scrollbar {
			width: 0.4rem; }
		&::-webkit-scrollbar-thumb {
			background-color: $active-icons;
			height: 20px; }

		h4 {
			font-size: 2rem;
			color: #464646; }
		h5 {
			@include ssp-light();
			font-size: 1.4rem;
			text-transform: uppercase;
			font-style: italic;
			margin-bottom: 3.5rem; }
		p {
			font-size: 1.4rem;
			text-align: right;
			line-height: 1.8;
			margin-bottom: 5rem; } }
	&-person_skill {
		box-sizing: border-box;
		width: 80%;
		margin-bottom: 3.5rem;
		@media screen and (max-width: 540px) {
			width: 100%; }
		.skill_description {
			display: flex;
			justify-content: space-between;
			margin-bottom: 0.5rem;
			p {
				text-transform: uppercase;
				font-size: 1.6rem;
				margin-bottom: 0; } }
		.skill_progress {
			display: block;
			width: 100%;
			height: 0.5rem;
			background-color: $progress-bg;
			.skill_value {
				background-color: $active-icons;
				height: 0.5rem;
				width: 1%; }
			#sv65 {
				width: 65%; }
			#sv75 {
				width: 75%; }
			#sv80 {
				width: 80%; }
			#sv85 {
				width: 85%; } } }

	&-slider_dots {
		margin-top: 1rem; }
	&-slider_dot {
		width: 1rem;
		height: 1rem;
		border: 1px solid $active-icons;
		border-radius: 50%;
		margin-left: 1rem;
		cursor: pointer;
		transition: background-color 0.6s ease;
		&_active {
			background-color: $active-icons; } } }

@keyframes fade {
	from {
		opacity: 0.4; }
	to {
		opacity: 1; } }












